.bar {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 27px;
  height: 2px;
  background: #fff; }

.bar-active .bar {
  background: 0 0; }

.bar::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 27px;
  height: 2px;
  background: #fff;
  transition: 0.3s;
  transform: translateY(-8px); }

.bar-active .bar::before {
  transform: rotate(-45deg); }

.bar::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 27px;
  height: 2px;
  background: #fff;
  transition: 0.3s;
  transform: translateY(8px); }

